/* imports */
@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;700&display=swap');

/*Variables*/
$fuente--principal: 'Roboto Mono',serif;
$box-shadow : -1px 10px 9px 3px rgba(212,212,212,1);
$color-primario: rgb(1, 146, 134);
$color-secundario:  rgba(0, 218, 218,.1);

body {
    margin: 0;
    background-color: $color-secundario;
    font-family: $fuente--principal;
}

.JobOffer--container {
    
    display: flex;
    flex-direction: row;
    background-color: white;
    border: 1px hidden rgba(212,212,212,1) ;
    box-shadow: $box-shadow;
    border-radius: 5px;
    width: 80vw;
    justify-content: space-between;
    img {
        width: 100px;
        height: 100px;
        margin: auto 0;
        padding: 0 10px;
    }
    h3 {
        margin: 0;
        color: black;
    }
    h3:hover {  
        color:$color-primario; 
        }
    p {
     padding: 0 10px;
    }
}

.JobOffer--information-container {
    display: flex;
    flex-direction: row;
}

.JobOffer--information {
    display: flex;
    flex-direction: column;
}

.JobOffer--tags {
    display: flex;
    align-items: center;
    p{
    margin-right: 5px;
    padding: 5px;
    color: $color-primario;
    background-color:$color-secundario;
    border-radius: 5px;
    font-weight: bold;
    }
    p:hover {
    background-color: $color-primario;  
    color: white;        
    }
}


/* Media Queries */

/* Portrait and Landscape */
@media only screen 
  and (min-device-width: 320px) 
  and (max-device-width: 480px)
  and (-webkit-min-device-pixel-ratio: 2) {

    .JobOffer--container {
        display: block;
        margin-right: 10px;
        margin-left: 0;
        img {
        display: none;    
        }
    }

    .JobOffer--information-container {
        display: block;
        margin-left: 5px;
    }

    .JobOffer--tags {
        border-top: 1px solid  rgba(212,212,212,.5);
        flex-wrap: wrap;
        margin-left: 5px;
    }


}