@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{margin:0;background-color:rgba(0,218,218,0.1);font-family:"Roboto Mono",serif}.JobOffer--container{display:flex;flex-direction:row;background-color:white;border:1px hidden #d4d4d4;box-shadow:-1px 10px 9px 3px #d4d4d4;border-radius:5px;width:80vw;justify-content:space-between}.JobOffer--container img{width:100px;height:100px;margin:auto 0;padding:0 10px}.JobOffer--container h3{margin:0;color:black}.JobOffer--container h3:hover{color:#019286}.JobOffer--container p{padding:0 10px}.JobOffer--information-container{display:flex;flex-direction:row}.JobOffer--information{display:flex;flex-direction:column}.JobOffer--tags{display:flex;align-items:center}.JobOffer--tags p{margin-right:5px;padding:5px;color:#019286;background-color:rgba(0,218,218,0.1);border-radius:5px;font-weight:bold}.JobOffer--tags p:hover{background-color:#019286;color:white}@media only screen and (min-device-width: 320px) and (max-device-width: 480px) and (-webkit-min-device-pixel-ratio: 2){.JobOffer--container{display:block;margin-right:10px;margin-left:0}.JobOffer--container img{display:none}.JobOffer--information-container{display:block;margin-left:5px}.JobOffer--tags{border-top:1px solid rgba(212,212,212,0.5);flex-wrap:wrap;margin-left:5px}}

.header--container{background-image:url(/job-listings.git/static/media/bg-header-desktop.6e2f7196.svg);background-color:#019286;width:100%;height:150px}

.jobOfferContainer{display:flex;flex-direction:column;align-items:center}.jobOfferContainer li{list-style:none;padding:10px 0}.form--job-filter{display:flex;flex-direction:column;justify-content:center;align-items:center}.form--job-filter input{width:300px;height:20px;border:none;border-radius:5px;box-shadow:-1px 10px 9px 3px #d4d4d4;margin:20px 0}

