$color-primario: rgb(1, 146, 134);
$box-shadow : -1px 10px 9px 3px rgba(212,212,212,1);

.jobOfferContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    li {
        list-style: none;
        padding: 10px 0;
    }
    
}

.form--job-filter {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    input {
        width: 300px;
        height: 20px;
        border: none;
        border-radius: 5px;
        box-shadow: $box-shadow;
        margin: 20px 0;
    }
}